import React from 'react';
import './about.scss';

const About = React.forwardRef((props, ref) => {
  return (
    <section  ref={ref} id="about" className="about">
      <div className="logo-container">
        <div className='logo'></div>
      </div>
      <div className="description">
        <h2>O Nas</h2>
        <p>
          Witamy w Leasing i Kredyty! Jestemy wiodc firm oferujc 
          innowacyjne rozwizania finansowe dla naszych klientw w caej Polsce. 
          Nasz zesp ekspertw jest gotowy, aby pomc Ci w znalezieniu najlepszego 
          rozwizania leasingowego i kredytowego, dostosowanego do Twoich potrzeb.
        </p>
        <p>
          Nasza misja to zapewnienie najwyszej jakoci usug i wsparcia, aby 
          Twoje finansowe decyzje byy proste i korzystne. Dziki wieloletniemu 
          dowiadczeniu i szerokiej wiedzy na temat rynku, gwarantujemy, e jeste 
          w dobrych rkach.
        </p>
      </div>
    </section>
  );
})

export default About;
